    okClick() {
        var q : HTMLElement = this.element.nativeElement;
        this.currentQuestion.options.map(option => {
            if(option.correct) {
                var optionButton = q.getElementsByClassName(option.id)[0];
                
                if(optionButton.classList.contains('active')) {
                    this.correctAnswres = this.correctAnswres + 1;
                    console.log("correct");
                }
                
                optionButton.classList.remove('btn-default');
                optionButton.classList.add('btn-success');
                optionButton.classList.add('btn-fill');
            } else {
                var optionButton = q.getElementsByClassName(option.id)[0];
                
                if(optionButton.classList.contains('active')) {
                    optionButton.classList.remove('btn-default');
                    optionButton.classList.add('btn-danger');
                    optionButton.classList.add('btn-fill');
                }
            }
        });
    }
    
    nextClick() {
        this.currentIndex = this.currentIndex + 1;
        if(this.currentIndex >= this.examQuestions.length){
            this.router.navigate(["/result"], {
                queryParams: {
                    result: this.correctAnswres,
                    questions: this.examQuestions.length
                }
            });
        }
        this.currentQuestion = this.examQuestions[this.currentIndex];
    }
    
    shuffle() {
      var currentIndex = this.examQuestions.length, temporaryValue, randomIndex;

      // While there remain elements to shuffle...
      while (0 !== currentIndex) {

        // Pick a remaining element...
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex -= 1;

        // And swap it with the current element.
        temporaryValue = this.examQuestions[currentIndex];
        this.examQuestions[currentIndex] = this.examQuestions[randomIndex];
        this.examQuestions[randomIndex] = temporaryValue;
      }
    }
    
    generateExamQuestions() {
        //10 tarea 1
        this.selectRandomQuestions(this.questions1, 10);
        //3 tarea 2
        this.selectRandomQuestions(this.questions2, 3);
        //2 tarea 3
        this.selectRandomQuestions(this.questions3, 2);
        //3 tarea 4
        this.selectRandomQuestions(this.questions4, 3);
        //7 tarea 5
        this.selectRandomQuestions(this.questions5, 7);
        
        
        //15 of 25
    }
    
    selectRandomQuestions(questions: any[], count: number) {
        var dictionary = {
            500: true
        };
        
        for (var i = 0; i < count; i++) {
            var random = 500;
            while(dictionary[random]) {
                var random = this.randomIntFromInterval(0, questions.length - 1);
            }
            dictionary[random] = true;
            
            this.examQuestions.push(questions[random]);   
        }
    }
    
    randomIntFromInterval(min,max) {
        return Math.floor(Math.random()*(max-min+1)+min);
    }
    
    reset() {
        this.currentIndex = 0;
        this.correctAnswres = 0;
        this.examQuestions = [];
    }
